<template>
    <div class="container" id="new-project"> 
        <h3>مشروع جديد</h3>
        <form @submit="saveProject">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="project_id" placeholder="الرقم التعريفي للمشروع" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="project_desc" placeholder="وصف المشروع" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="project_info" placeholder="بيانات عن المشروع" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="residual" placeholder="المتبقي" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="date" v-model="to_start" placeholder="موعد البدء" >
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="date" v-model="to_end" placeholder="موعد النهو" >
                </div>
            </div>

            <button type="submit" class="btn">تسجيل المشروع</button>
            <router-link class="btn grey left" :to="{name: 'view-main-job', params: {main_job_id: $route.params.main_job_id}}">إلغاء</router-link>
            <br>
            <br>
        </form>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'new-project',
    data() {
        return {
            main_job_id: null,
            project_id: null,
            project_desc: null,
            project_info: null,
            residual: null,
            to_start: null,
            to_end: null,
        }
    },
    methods: {
        saveProject() {
            db.collection('projects').add({
                main_job_id: this.$route.params.main_job_id,
                project_id: this.project_id,
                project_desc: this.project_desc,
                project_info: this.project_info,
                residual: this.residual,
                to_start: this.to_start,
                to_end: this.to_end,
            })
            .then(() => {
                this.$router.push('/')
            })
        }
    }
}
</script>


