<template>
    <div class="container">
        <ul class="collection with-header">
            <li class="collection-header center"><h4>المشروعات</h4></li>
            <div v-for="project in projects" v-bind:key="project.project_desc">
                <li class="collection-item">
                    <div class="chip">{{project.project_id}}: {{project.project_desc}}</div>
                    <router-link class="secondary-content left"
                        :to="{name: 'show-one-project', params: {main_job_id: project.main_job_id, project_id: project.project_id}}">
                        تفاصيل
                    </router-link>
                </li>
            </div>
        </ul>
    </div>
</template>

<script>
import firebase from '../firebaseInit'
export default {
    name: 'show-project',
    data() {
        return {
            projects: [],
        }
    },
    mounted() {
        firebase.collection('projects').orderBy('project_id').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if (doc.data().main_job_id == this.$route.params.main_job_id) {
                    this.projects.push(doc.data())
                }
            })
        })
    }
}
</script>


