<template>
    <div class="container">
        <h3>إضافة مجموعة للمشروعات</h3>
        <form @submit="saveField" class="s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="main_job_id" placeholder="الرقم التعريفي لمجموعة المشروعات" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="name" placeholder="اسم مجموعة المشروعات" required>
                </div>
            </div>
            <button type="submit" class="btn lite-blue right">تسجيل المجموعة</button>
            <router-link to="/" class="btn grey left">إلغاء</router-link>
        </form>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'new-main-job',
    data() {
        return {
            main_job_id: null,
            name: null,     
        }
    },
    methods: {
        saveField() {
            db.collection('main_jobs').add({
                main_job_id: this.main_job_id,
                name: this.name,
            })
            .then(() => {
                this.$router.push('/')
            })
        },
    }
}
</script>


