<template>
    <div class="container">
        <h4>تعديل بيانات الشركة للقطاع رقم "{{section_id}}" للمشروع رقم "{{project_id}}" للمجموعة رقم "{{main_job_id}}"</h4>
        <form @submit="updateSection" class="s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="company_id" placeholder="الرقم التعريفي للشركة">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="company_name" placeholder="اسم الشركة">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number"  step="0.001" v-model="spent" placeholder="المنصرف">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="execution_rate" placeholder="نسبة التنفيذ الفعلية">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number"  step="0.001" v-model="contract_value" placeholder="قيمة التعاقد للشركة">
                </div>
            </div>

            <button type="submit" class="btn lite-blue">تعديل بيانات الشركة</button>
            <router-link :to="{name: 'view-project'}" class="btn grey left">إلغاء</router-link>
            <br>
            <br>
        </form>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'edit-company',
    data() {
        return {
            main_job_id: null,
            project_id: null,
            section_id: null,
            company_id: null,
            company_name: null,
            spent: null,
            execution_rate: null,
            contract_value: null,
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('companies').where('company_id', '==', to.params.company_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.main_job_id = doc.data().main_job_id
                    vm.project_id = doc.data().project_id
                    vm.section_id = doc.data().section_id
                    vm.company_id = doc.data().company_id
                    vm.company_name = doc.data().company_name
                    vm.spent = doc.data().spent
                    vm.execution_rate = doc.data().execution_rate
                    vm.contract_value = doc.data().contract_value
                })
            })
        })
    },
    methods: {
        updateSection() {
            db.collection('companies').where('company_id', '==', this.$route.params.company_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            main_job_id: this.main_job_id,
                            project_id: this.project_id,
                            section_id: this.section_id,
                            company_id: this.company_id,
                            spent: this.spent,
                            execution_rate: this.execution_rate,
                            contract_value: this.contract_value
                        })
                        .then(() => {
                            this.$router.go(-1)
                        })
                    })
                })
        }
    }
}
</script>


