<template>
    <div class="container">
        <h3>تعديل بيانات المشروع الرئيسي</h3>
        <form @submit="updateMainJob" class="s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="main_job_id" placeholder="الرقم التعريفي للمشروع الرئيسي" required disabled>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="name" placeholder="اسم المشروع الرئيسي" required>
                </div>
            </div>

            <button type="submit" class="btn">تعديل بيانات المشروع الرئيسي</button>
            <router-link :to="{name: 'view-main-job'}" class="btn grey left">الغاء</router-link>
        </form>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'edit-main-job',
    data() {
        return {
            main_job_id: null,
            name: null,
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('main_jobs').where('main_job_id', '==', to.params.main_job_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.main_job_id = doc.data().main_job_id
                    vm.name = doc.data().name
                })
            })
        })
    },
    methods: {
        updateMainJob() {
            db.collection('main_jobs').where('main_job_id', '==', this.$route.params.main_job_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            main_job_id: this.main_job_id,
                            name: this.name,
                        })
                        .then(() => {
                            this.$router.go(-1)
                        })
                    })
                })
        }
    }
}
</script>


