<template>
    <div class="container" id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header center"><h4>متابعة المشروعات</h4></li>
            <div v-for="main_job in main_jobs" v-bind:key="main_job.main_job_id">
                <li class="collection-item">
                    <div class="chip">{{main_job.main_job_id}}: {{main_job.name}}</div>
                    <router-link class="secondary-content left"
                        v-bind:to="{name: 'view-main-job', params: {main_job_id: main_job.main_job_id}}">
                        تفاصيل
                    </router-link>
                    <button v-if="role == 'admin'" @click="deleteMainJob(main_job.main_job_id, main_job.name)" class="red btn">محو المجموعة</button>
                </li>
            </div>
        </ul>

        <div class="fixed-action-btn">
            <router-link :to="{name: 'new-main-job'}" class="btn-floating btn-large blue">
                اضافة
            </router-link>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data() {
        return {
            role: false,
            main_jobs: [],
        }
    },
    mounted() {
        // db.collection('sections').add({
        //                                                    main_job_id:"1",
        //             project_id: "1",
        //             section_id:"5" ,
        //             section_desc: "الاجمالي",
        //             contract_value: 7288.76,
        //             approved_20: 6875.429,
        //             approved_20_21: 700.904,
        //             total_approved_21: 7576.333,
        //             to_start :"" ,
        //             to_end: ""  ,                       
        //             section_info:'',
        // })
        db.collection('users').doc(firebase.auth().currentUser.uid).get().then(snapshot => {
            this.role = snapshot.data().role
        })
        db.collection('main_jobs').get().then(querySnapshot =>  {
            querySnapshot.forEach(doc => {
                const data = {
                    'main_job_id': doc.data().main_job_id,
                    'name': doc.data().name,
                }
                this.main_jobs.push(data)
            });
        })
    },
    methods: {
        deleteMainJob(id, jobName) {
            if (this.role == 'admin') {
                if (confirm(`Are you sure you want to delete main project with name ${jobName}`)) {
                    db.collection('main_jobs').where('main_job_id', '==', id).get().then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                        })
                    })
                    db.collection('projects').where('main_job_id', '==', id).get().then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                        })
                    })
                    db.collection('sections').where('main_job_id', '==', id).get().then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                        })
                    })
                    db.collection('companies').where('main_job_id', '==', id).get().then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                        })
                    })
                    this.$router.go({path: this.$router.path})
                }
            } else {
                alert('This priviledge is only for the admin.')
            }
        }
    }
}
</script>


