<template>
    <div class="container" id="home">
        <h3></h3>
        <ul class="collection with-header">
            <li class="collection-header center"><h4>عرض المشروعات</h4></li>
            <div v-for="main_job in main_jobs" v-bind:key="main_job.main_job_id">
                <li class="collection-item">
                    <div class="chip">{{main_job.main_job_id}}: {{main_job.name}}</div>
                    <router-link class="secondary-content left"
                        v-bind:to="{name: 'show-project', params: {main_job_id: main_job.main_job_id}}">
                        تفاصيل
                    </router-link>
                </li>
            </div>
        </ul>
    </div>
</template>

<script>
import firebase from '../firebaseInit'
export default {
    name: 'home',
    data() {
        return {
            main_jobs: [],
        }
    },
    mounted() {
        firebase.collection('main_jobs').orderBy('main_job_id').get().then(snapshot => {
            snapshot.forEach(doc => {
                this.main_jobs.push(doc.data())
            })
        })
    }
}
</script>


