<template>
    <div  class="container">
        <ul class="collection with-header">
            <li class="collection-header center"><h5>المشروعات الموجودة في مجموعة: {{name}}</h5></li>
            <div v-for="project in projects" v-bind:key="project.project_id">
                <li class="collection-item">
                    <div class="chip">{{project.project_id}}: {{project.project_desc}}</div>
                    <router-link class="secondary-content left"
                        :to="{name: 'view-project', params: {main_job_id: project.main_job_id, project_id: project.project_id}}">
                        تفاصيل
                    </router-link>
                </li>
            </div>
        </ul>
        <router-link to="/" class="btn grey right">رجوع</router-link>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-main-job', params: {main_job_id: $route.params.main_job_id}}" class="btn-floating btn-large green">
                تعديل
            </router-link>
            <router-link :to="{name: 'new-project', params: {main_job_id: $route.params.main_job_id}}" class="btn-floating btn-large blue">
                اضافة
            </router-link>
        </div>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'view-main-job',
    data() {
        return {
            name,
            projects: []
        }
    },
    mounted() {
        db.collection('main_jobs').where('main_job_id', '==', this.$route.params.main_job_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.name = doc.data().name
                })
            })
        db.collection('projects').where('main_job_id', '==', this.$route.params.main_job_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.projects.push(doc.data())
                })
            })
    }
}
</script>


