<template>
    <div class="container" id="edit-project">
        <h3 class="right">تعديل بيانات المشروع رقم: {{project_id}}</h3>
        <div class="row">
            <form @submit="updateProject" class="s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model="project_id" placeholder="الرقم التعريفي للمشروع" disabled required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_desc" placeholder="وصف المشروع" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_info" placeholder="بيانات المشروع" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" step="0.001" v-model="residual" placeholder="المتبقي" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="date" v-model="to_start" placeholder="تاريخ البدء" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="date" v-model="to_end" placeholder="تاريخ النهو" required>
                    </div>
                </div>

                <button type="submit" class="btn">تعديل بيانات المشروع</button>
                <router-link :to="{name: 'view-project'}" class="btn grey left">إلغاء</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'edit-project',
    data() {
        return {
            project_id: null,
            project_desc: null,
            project_info: null,
            residual: null,
            to_start: null,
            to_end: null,
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('projects').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if (doc.data().project_id == to.params.project_id && doc.data().main_job_id == to.params.main_job_id) {
                    next(vm => {
                        vm.project_id = doc.data().project_id
                        vm.project_desc = doc.data().project_desc
                        vm.project_info = doc.data().project_info
                        vm.residual = doc.data().residual
                        vm.to_start = doc.data().to_start
                        vm.to_end = doc.data().to_end
                    })
                }
            })
        })
    },
    methods: {
        updateProject() {
            db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            project_id: this.project_id,
                            project_desc: this.project_desc,
                            project_info: this.project_info,
                            residual: this.residual,
                            to_start: this.to_start,
                            to_end: this.to_end,
                        })
                        .then(() => {
                            this.$router.go(-1)
                        })
                    })
                })
        }
    }
}
</script>


