<template>
    <div class="container" id="view-project">
        <div id="reactButtons">
            <router-link v-bind:to="{name: 'view-main-job', params: {main_job_id: $route.params.main_job_id}}" class="btn grey">رجوع إلى القائمة السابقة</router-link>
            <router-link to="/" class="btn grey">الذهاب إلى متابعة المشروعات</router-link>
            <button v-if="role == 'admin'" @click="deleteProject" class="btn red left">محو المشروع</button>
        </div>
        <ul class="collection with-header">
            <li v-if="project.project_id" class="collection-header"><h4>رقم المشروع: {{project.project_id}}</h4></li>
            <li v-if="project.project_desc" class="collection-header"><h4>المشروع: {{project.project_desc}}</h4></li>
            <li v-if="project.project_info" class="collection-header"><h4>بيانات المشروع: {{project.project_info}}</h4></li>
            <li v-if="project.residual" class="collection-header"><h4>المتبقي: {{project.residual}}</h4></li>
            <li v-if="project.to_start" class="collection-header"><h4>تاريخ البدء: {{project.to_start}}</h4></li>
            <li v-if="project.to_end" class="collection-header"><h4>تاريخ النهو: {{project.to_end}}</h4></li>
            <li v-if="project.project_id" class="collection-header"><h3>القطاعات <router-link v-bind:to="{name: 'new-section', params: {main_job_id: $route.params.main_job_id, project_id: $route.params.project_id}}" class="btn blue left">
                إضافة قطاع
            </router-link></h3></li>
        
            <div v-for="section in sections" v-bind:key="section.section_desc">
                <div v-if="section.main_job_id == project.main_job_id">
                    <li v-if="section.section_id" class="collection-header"><h3>القطاع رقم {{ section.section_id }}
                        <router-link v-bind:to="{name: 'edit-section', params: {section_id: section.section_id}}" class="btn green">
                            تعديل بيانات القطاع
                        </router-link>
                        <button v-if="role == 'admin'" @click="deleteSection(section.section_id)" class="red btn left">محو القطاع</button>
                    </h3></li>
                    <li v-if="section.section_desc" class="collection-header"><h4>الوصف: {{section.section_desc}}</h4></li>
                    <li v-if="section.contract_value" class="collection-header"><h4>قيمة التعاقد: {{section.contract_value}}</h4></li>
                    <li v-if="section.approved_20" class="collection-header"><h4>المعتمد حتى 30/6/2020: {{section.approved_20}}</h4></li>
                    <li v-if="section.approved_20_21" class="collection-header"><h4>المعتمد 2020/2021: {{section.approved_20_21}}</h4></li>
                    <li v-if="section.total_approved_21" class="collection-header"><h4>المعتمد حتى 30/6/2021: {{section.total_approved_21}}</h4></li>
                    <li v-if="section" class="collection-header"><h3>الشركات <router-link v-bind:to="{name: 'new-company', params: {section_id: section.section_id}}" class="btn blue left">
                        إضافة شركة
                    </router-link></h3></li>
                    <div v-for="company in companies" v-bind:key="company.company_name && company.spent">
                        <div v-if="company.section_id == section.section_id">
                            <li v-if="company.company_id" class="collection-header">
                                <h3>الشركة رقم {{ company.company_id }} <router-link v-bind:to="{name: 'edit-company', 
                                    params: {main_job_id: company.main_job_id, project_id: company.project_id, 
                                        section_id: company.section_id, 
                                    company_id: company.company_id}}" class="btn green">
                                    تعديل بيانات الشركة
                                </router-link>
                                <button v-if="role == 'admin'" @click="deleteCompany(company.company_id, section.section_id)" class="btn red left">محو الشركة</button>
                                </h3>
                            </li>
                            <li v-if="company.company_name" class="collection-header"><h4>الشركة المنفذة: {{company.company_name}}</h4></li>
                            <li v-if="company.spent" class="collection-header"><h4>المنصرف: {{company.spent}}</h4></li>
                            <li v-if="company.execution_rate" class="collection-header"><h4>نسبة التنفيذ الفعلي: {{company.execution_rate}}</h4></li>
                            <li v-if="company.contract_value" class="collection-header"><h4>قيمة التعاقد للشركة: {{company.contract_value}}</h4></li>
                        </div>
                    </div>
                </div>
            </div>
        </ul>
        <br>
        <br>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-project'}" class="btn-floating btn-large green">
                تعديل
            </router-link>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from '../firebaseInit'
export default {
    name: 'view-project',
    data() {
        return {
            project: {},
            sections: [],
            companies: [],
            id: null,
            role: false,
        }
    },
    mounted() {
        db.collection('projects').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if (doc.data().main_job_id == this.$route.params.main_job_id && doc.data().project_id == this.$route.params.project_id) {
                    this.project = doc.data()
                }
            })
        })
        db.collection('sections').orderBy('section_id').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if (doc.data().main_job_id == this.$route.params.main_job_id && doc.data().project_id == this.$route.params.project_id) {
                    this.sections.push(doc.data())
                }
            })
        })
        db.collection('companies').orderBy('company_id').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if (doc.data().main_job_id == this.$route.params.main_job_id && doc.data().project_id == this.$route.params.project_id) {
                    this.companies.push(doc.data())
                }
            })
        })
        db.collection('users').doc(firebase.auth().currentUser.uid).get().then(snapshot => {
            this.role = snapshot.data().role
        })
    },
    methods: {
        deleteProject() {
            if (this.role == 'admin') {
                if(confirm('Are you sure?')) {
                    db.collection('projects').onSnapshot(snapshot => {
                        snapshot.forEach(doc => {
                            if (doc.data().project_id == this.$route.params.project_id && doc.data().main_job_id == this.$route.params.main_job_id) {
                                db.collection('sections').where('project_id', '==', doc.data().project_id).get()
                                    .then(querySnapshot => {
                                        querySnapshot.forEach(section => {
                                            section.ref.delete()
                                        })
                                    })
                                db.collection('companies').where('project_id', '==', doc.data().project_id).get()
                                    .then(querySnapshot => {
                                        querySnapshot.forEach(company => {
                                            company.ref.delete()
                                        })
                                    })
                                doc.ref.delete()
                                this.$router.push('/')
                            }
                        })
                    })
                }
            } else {
                alert('An admin only can delete a project.')
            }
        },
        deleteSection(id) {
            if (this.role == 'admin') {
                if(confirm('Are you sure?')) {
                    db.collection('sections').where('section_id', '==', id).get()
                        .then(querySnapshot => {
                            querySnapshot.forEach(section => {
                                if ((section.data().main_job_id == this.$route.params.main_job_id) && (section.data().project_id == this.$route.params.project_id)){
                                    db.collection('companies').where('section_id', '==', section.data().section_id).get()
                                        .then(querySnapshot => {
                                            querySnapshot.forEach(company => {
                                                company.ref.delete()
                                            })
                                        })
                                    section.ref.delete()
                                    this.$router.push('/')
                                }
                            })
                        })
                }
            } else {
                alert('An admin only can delete a section.')
            }
        },
        deleteCompany(companyId, sectionId) {
            if (this.role == 'admin') {
                if (confirm('Are you sure?')) {
                    db.collection('companies').where('company_id', '==', companyId).get()
                        .then(querySnapshot => {
                            querySnapshot.forEach(company => {
                                if ((company.data().main_job_id == this.$route.params.main_job_id) &&
                                    (company.data().project_id == this.$route.params.project_id) &&
                                    (company.data().section_id == sectionId)) {
                                        company.ref.delete()
                                        this.$router.push('/')
                                    }
                            })
                        })
                }
            } else {
                alert('An admin only can delete a company.')
            }
        }
    }
}
</script>

<style scoped>
#reactButtons {
    padding-top: 1%;
}
</style>


