<template>
    <div class="container">
        <h4>تعديل بيانات القطاع للمشروع رقم "{{this.project_id}}" للمجموعة رقم "{{this.main_job_id}}"</h4>
        <form @submit="updateSection" class="s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="section_id" placeholder="الرقم التعريفي للقطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_desc" placeholder="وصف القطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_info" placeholder="بيانات عن القطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="contract_value" placeholder="قيمة التعاقد" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="approved_20" placeholder="المعتمد حتى 30/6/2020" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="approved_20_21" placeholder="المعتمد 2020/2021" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="total_approved_21" placeholder="اجمالي المعتمد حتى 30/6/2021" required>
                </div>
            </div>

            <button type="submit" class="btn lite-blue">تعديل بيانات القطاع</button>
            <router-link v-bind:to="{name: 'view-project'}" class="btn grey left">إالغاء</router-link>
            <br>
            <br>
        </form>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'edit-section',
    data() {
        return {
            main_job_id: null,
            project_id: null,
            section_id: null,
            section_desc: null,
            section_info: null,
            contract_value: null,
            approved_20: null,
            approved_20_21: null,
            total_approved_21: null,
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('sections').where('section_id', '==', to.params.section_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.main_job_id = doc.data().main_job_id
                    vm.project_id = doc.data().project_id
                    vm.section_id = doc.data().section_id
                    vm.section_desc = doc.data().section_desc
                    vm.section_info = doc.data().section_info
                    vm.contract_value = doc.data().contract_value
                    vm.approved_20 = doc.data().approved_20
                    vm.approved_20_21 = doc.data().approved_20_21
                    vm.total_approved_21 = doc.data().total_approved_21
                })
            })
        })
    },
    methods: {
        updateSection() {
            db.collection('sections').where('section_id', '==', this.$route.params.section_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            main_job_id: this.main_job_id,
                            project_id: this.project_id,
                            section_id: this.section_id,
                            section_desc: this.section_desc,
                            section_info: this.section_info,
                            contract_value: this.contract_value,
                            approved_20: this.approved_20,
                            approved_20_21: this.approved_20_21,
                            total_approved_21: this.total_approved_21
                        })
                        .then(() => {
                            this.$router.go(-1)
                        })
                    })
                })
        }
    }
}
</script>


