<template>
    <div class="container">
        <h3>إضافة شركة جديدة</h3>
        <form @submit="saveCompany">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="company_id" placeholder="الرقم التعريفي للشركة">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="company_name" placeholder="اسم الشركة">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="spent" placeholder="المنصرف">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="execution_rate" placeholder="نسبة التنفيذ الفعلية">
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="contract_value" placeholder="قيمة التعاقد للشركة">
                </div>
            </div>
            <button type="submit" class="btn right">تسجيل الشركة</button>
            <router-link :to="{name: 'view-project', params: {main_job_id: $route.params.main_job_id, project_id: $route.params.project_id}}" class="btn grey left">إلغاء</router-link>
            <br>
            <br>
        </form>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'new-company',
    data() {
        return {
            main_job_id: null,
            project_id: null,
            section_id: null,
            company_id: null,
            company_name: null,
            spent: null,
            execution_rate: null,
            contract_value: null,
        }
    },
    methods: {
        saveCompany() {
            db.collection('companies').add({
                main_job_id: this.$route.params.main_job_id,
                project_id: this.$route.params.project_id,
                section_id: this.$route.params.section_id,
                company_id: this.company_id,
                company_name: this.company_name,
                spent: this.spent,
                execution_rate: this.execution_rate,
                contract_value: this.contract_value,
            })
            .then(() => {
                this.$router.push('/')
            })
        }
    }
}
</script>

<style scoped>
    #navLinks {
        padding-top: 1%;
    }
</style>>


