<template>
    <div>
        <table class="container">
            <tr>
                <th>م</th>
                <th>المشروع</th>
                <th>قطاعات المشروع</th>
                <th>الشركة المنفذة</th>
            
                <th>القيمة التعاقدية</th>
                <th>المعتمد حتى 30/6/2020</th>
                <th>المعتمد 2020/2021</th>
                <th>إجمالي المعتمد 30/6/2021</th>
                <th>المتبقي</th>
                <th>المنصرف</th>
            
                <th>نسبة التنفيذ الفعلي</th>    
                <th>تاريخ البدء</th>
                <th>تاريخ النهو</th>

                <th>بيانات المشروع</th>
            </tr>
            <tr>
                <td>{{project.project_id}}</td>
                <td>{{project.project_desc}}</td>
                <td>
                    <ul v-for="section in sections" v-bind:key="section.section_desc">
                        <td>{{section.section_id}}: {{section.section_desc}}</td>
                    </ul>
                </td>
                <td>
                    <ul v-for="company in companies" v-bind:key="company.company_id && company.company_name && company.spent">
                        <li>{{company.section_id}}: {{company.company_name}}</li>
                    </ul>
                </td>
                <td>
                    <ul v-for="section in sections" v-bind:key="section.contract_value">
                        <li>{{section.section_id}}: {{section.contract_value}}</li>
                    </ul>
                </td>
                <td>
                    <ul v-for="section in sections" v-bind:key="section.approved_20">
                        <li>{{section.section_id}}: {{section.approved_20}}</li>
                    </ul>
                </td>
                <td>
                    <ul v-for="section in sections" v-bind:key="section.approved_20_21">
                        <li>{{section.section_id}}: {{section.approved_20_21}}</li>
                    </ul>
                </td>
                <td>
                    <ul v-for="section in sections" v-bind:key="section.total_approved_21">
                        <li>{{section.section_id}}: {{section.total_approved_21}}</li>
                    </ul>
                </td>
                <td>{{project.residual}}</td>
                <td>
                    <ul v-for="company in companies" v-bind:key="company.spent">
                        <li>{{company.section_id}}: {{company.spent}}</li>
                    </ul>
                </td>
                <td>
                    <ul v-for="company in companies" v-bind:key="company.execution_rate && company.spent">
                        <li>{{company.section_id}}: {{company.execution_rate}}</li>
                    </ul>
                </td>
                <td>{{project.to_start}}</td>
                <td>{{project.to_end}}</td>
                <td>{{project.project_info}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'show-one-project',
    data() {
        return {
            project: {},
            sections: [],
            companies: [],
        }
    },
    mounted() {
        db.collection('projects').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if (doc.data().main_job_id == this.$route.params.main_job_id && doc.data().project_id == this.$route.params.project_id) {
                    this.project = doc.data()
                }
            })
        })
        db.collection('sections').orderBy('section_id').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if(doc.data().main_job_id == this.$route.params.main_job_id && doc.data().project_id == this.project.project_id) {
                    this.sections.push(doc.data())
                }
            })
        })
        db.collection('companies').onSnapshot(snapshot => {
            snapshot.forEach(doc => {
                if (doc.data().main_job_id == this.$route.params.main_job_id && doc.data().project_id == this.project.project_id) {
                    this.companies.push(doc.data())
                }
            })
        })
    },
}
</script>

<style scoped>
#reactButtons {
    padding-top: 1%;
}
#com {
    margin-top: 2%;
}
#sec {
    margin-top: 2%;
}
</style>


