import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewMainJob from '@/components/new/NewMainJob'
import NewProject from '@/components/new/NewProject'
import NewSection from '@/components/new/NewSection'
import NewCompany from '@/components/new/NewCompany'
import EditMainJob from '@/components/edit/EditMainJob'
import EditProject from '@/components/edit/EditProject'
import EditSection from '@/components/edit/EditSection'
import EditCompany from '@/components/edit/EditCompany'
import ViewProject from '@/components/view/ViewProject'
import ViewMainJob from '@/components/view/ViewMainJob'
import Home from '@/components/normal-user/Home'
import ShowProject from '@/components/normal-user/ShowProject'
import ShowOneProject from '@/components/normal-user/ShowOneProject'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase'
import database from '../components/firebaseInit'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/preview/:main_job_id',
      name: 'show-project',
      component: ShowProject,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/preview/:main_job_id/:project_id',
      name: 'show-one-project',
      component: ShowOneProject,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/new/mainJob',
      name: 'new-main-job',
      component: NewMainJob,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/new/project/:main_job_id',
      name: 'new-project',
      component: NewProject,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/new/section/:main_job_id/:project_id',
      name: 'new-section',
      component: NewSection,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/new/company/:main_job_id/:project_id/:section_id',
      name: 'new-company',
      component: NewCompany,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/edit/project/:main_job_id/:project_id',
      name: 'edit-project',
      component: EditProject,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/edit/section/:main_job_id/:project_id/:section_id',
      name: 'edit-section',
      component: EditSection,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/edit/company/:main_job_id/:project_id/:section_id/:company_id',
      name: 'edit-company',
      component: EditCompany,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/edit/mainjob/:main_job_id',
      name: 'edit-main-job',
      component: EditMainJob,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/view/mainJob/:main_job_id',
      name: 'view-main-job',
      component: ViewMainJob,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
    {
      path: '/view/project/:main_job_id/:project_id',
      name: 'view-project',
      component: ViewProject,
      props: true,
      meta: {
        requiresAuth: true,
        isAdminModerator: true
      }
    },
  ],
})

router.beforeEach((to, from, next) => {
  // check for required auth gaurd
  if(to.matched.some(record => record.meta.requiresAuth)) {
    // check if not logged in
    if(!firebase.auth().currentUser) {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      if (to.matched.some(record => record.meta.isAdminModerator)) {
        database.collection('users').doc(firebase.auth().currentUser.uid).get().then(snapshot => {
          if (snapshot.data().role == 'admin' || snapshot.data().role == 'moderator') {
            next()
          } else {
            next({
              path: '/home',
              query: {
                redirect: to.fullPath
              }
            })
          }
        })
      } else {
        next()
      }
    }
  } else if(to.matched.some(record => record.meta.requiresGuest)) {
    if(firebase.auth().currentUser) {
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      // proceed
      next()
    }
  } else {
    next()
  }
})

export default router;


