<template>
    <div class="container">
        <h3>إضافة قطاع جديد</h3>
        <form @submit="saveSection" class="s12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" v-model="section_id" placeholder="الرقم التعريفي للقطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_desc" placeholder="وصف القطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="section_info" placeholder="بيانات عن القطاع" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="contract_value" placeholder="قيمة التعاقد" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="approved_20" placeholder="المعتمد حتى 30/6/2020" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="approved_20_21" placeholder="المعتمد 2020/2021" required>
                </div>
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="number" step="0.001" v-model="total_approved_21" placeholder="إجمالي المعتمد حتى 30/6/2021" required>
                </div>
            </div>

            <button type="submit" class="btn lite-blue">تسجيل القطاع</button>
            <router-link :to="{name: 'view-project'}" class="btn grey left">إلغاء</router-link>
            <br>
            <br>
        </form>
    </div>
</template>

<script>
import db from '../firebaseInit'
export default {
    name: 'new-section',
    data() {
        return {
            main_job_id: null,
            project_id: null,
            section_id: null,
            section_desc: null,
            section_info: null,
            contract_value: null,
            approved_20: null,
            approved_20_21: null,
            total_approved_21: null,
        }
    },
    methods: {
        saveSection() {
            db.collection('sections').add({
                main_job_id: this.$route.params.main_job_id,
                project_id: this.$route.params.project_id,
                section_id: this.section_id,
                section_desc: this.section_desc,
                section_info: this.section_info,
                contract_value: this.contract_value,
                approved_20: this.approved_20,
                approved_20_21: this.approved_20_21,
                total_approved_21: this.total_approved_21,
            })
            .then(() => {
                this.$router.push('/')
            })
        }        
    }
}
</script>

<style scoped>
    #navLinks {
        padding-top: 1%;
    }
</style>>



